body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  display: flex;
  flex-direction: row;
  height: 60px;
  width: 100%;
  background-color: #ffc8cd;
}
.nav__img {
  height: 40px;
  margin: 10px 20px;
  border-radius: 50%;
}
.nav__title {
  color: #555555;
  font-size: 20px;
  line-height: 60px;
}
.nav__list {
  flex: 1 1;
  margin-left: 40px;
  display: flex;
  flex-direction: row;
  list-style: none;
  line-height: 60px;
  color: #555555;
}
.nav__list .list__item {
  margin: 0 10px;
}

.home {
  height: 100%;
  background: url(/resume/static/media/bg.97eb0ebd.jpeg) center no-repeat;
  background-size: cover;
}
.home__container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.home__block {
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 400px;
  align-items: center;
  border-radius: 10px;
  opacity: 0.85;
  background-color: #eee;
}
.home__block .block__info {
  display: flex;
  flex-direction: column;
  margin-top:20px; 
}
.home__block .block__info .info__avatar {
  position: relative;
  display: flex;
  height: 100px;
  justify-content: center;
}
.home__block .block__info .info__avatar .avatar__line{
  position: absolute;
  display: inline-block;
  top: 50px;
  width: 70px;
  border-top: 1px solid #ccc ;
}
.home__block .block__info .info__avatar .avatar__line--left{
  left: -70px
}
.home__block .block__info .info__avatar .avatar__line--right{
  left: 120px
}
.home__block .block__info .info__avatar img {
  height: 100%;
  border-radius: 50%;
  position: relative;
  vertical-align: middle;
}
.home__block .block__info .info__name {
  padding-top: 20px;
  text-align: center;
  font-size: 26px;
  color: #555;
}
.home__block .block__job {
  width: 200px;
  padding-top: 20px;
  text-align: center;
  font-size: 18px;
  color: #555;
}
.home__block .block__concat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
  width: 200px;
}
.home__block .block__concat .concat__list {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.home__block .block__concat .concat__list .list__item {
  display: flex;
  flex-direction: row;
  margin: 5px 0;
  font-size: 0;
}
.home__block .block__concat .concat__list .list__item .item__icon {
  height: 20px;
}
.home__block .block__concat .concat__list .list__item .item__icon img {
  height: 100%;
}
.home__block .block__concat .concat__list .list__item .item__detail {
  padding-left: 10px;
  line-height: 20px;
  font-size: 16px;
  color: #555;
}
.home__block .block__link {
  display: flex;
  flex-direction: row;
  padding-top:20px; 
  list-style: none;
}
.home__block .block__link .link__item {
  height: 20px;
  padding: 0 10px;
}
.home__block .block__link .link__item img{
  height: 100%;
}

html,body{
  height: 100%;
}

#root,.App{height: 100%;}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.work {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  background: url(/resume/static/media/work.fb332284.jpg) no-repeat;
  background-size: cover;
  z-index: -999;
}
.work:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #eee;
  opacity: 0.8;
  z-index: -99;
}
.work__title {
  padding: 20px 0;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.work__title .title__line {
  display: inline-block;
  width: 150px;
  border-top: 1px solid #ccc ;
}
.work__title .title__text {
  padding: 0 10px;
  vertical-align: -webkit-baseline-middle;
  font-size: 22px;
  color: #686868;
}
.work__plane{
  position: relative;
  left:-80px;
  height: 80px;
  width: 80px;
  -webkit-animation: plane 15s ease-in-out infinite;
          animation: plane 15s ease-in-out infinite;
  -webkit-animation-delay:2s;
          animation-delay:2s;
}
@-webkit-keyframes plane{
  0%{
    left: -180px;
  }
  100%{
    left: 100%;
  }
}
@keyframes plane{
  0%{
    left: -180px;
  }
  100%{
    left: 100%;
  }
}
.work__plane img{
  height: 100%;
}
.work__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5% 15% 0; 
}
.work__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  color: #555
}
.work__block .block__year {
  display: flex;
  flex-direction: column;
  height: 150px;
  width: 150px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 5px solid #555;
}
.work__block .block__year .year__text {
  font-size: 25px;
}
.work__block .block__bussiness {
  padding-top: 20px;
  font-size: 20px;
}
.work__block .block__job {
  padding-top: 20px;
  font-size: 18px;
  color: #EEB422;
}
.work__block .block__detail {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}
.work__block .block__detail .detail__text{
  line-height: 30px;
  font-size: 16px;
}

.skill {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  background: url(/resume/static/media/skill.1f887f22.jpg) no-repeat;
  background-size: cover;
  z-index: -999;
}
.skill:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #eee;
  opacity: 0.8;
  z-index: -99;
}
.skill__title {
  padding: 20px 0;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.skill__title .title__line {
  display: inline-block;
  width: 150px;
  border-top: 1px solid #ccc ;
}
.skill__title .title__text {
  padding: 0 10px;
  vertical-align: -webkit-baseline-middle;
  font-size: 22px;
  color: #686868;
}
.skill__container {
  flex: 1 1;
  padding: 0 10%;
}
.skill__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
}
.skill__list .list__card {
  width: calc(20%);
  height: calc(50%);
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
.skill__list .list__card:hover .card__front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.skill__list .list__card:nth-child(even):hover .card__front {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}
.skill__list .list__card:hover .card__back {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  z-index: 1;
}
.skill__list .list__card:nth-child(even):hover .card__back {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  z-index: 1;
}
.skill__list .list__card .card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 60%;
  width: 70%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: ease-in-out 600ms;
  transition: ease-in-out 600ms;
  border-radius: 6px;
}
.skill__list .list__card .card__front {
  background-position: center;
  background-size: cover;
  overflow: hidden;
}
.skill__list .list__card .card__back {
  display: flex;
  flex-direction: column;
  padding: 0 5px;
  background: #fff;
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}
.skill__list .list__card .card__back .back__title {
  padding-bottom: 10px;
  text-shadow: 0 1px 2px #555;
  font-size: 24px;
  font-weight: 500;
}
.skill__list .list__card .card__back .back__intro {
  font-size: 14px;
}

.project {
  position: relative;
  display: flex;
