  background-size: cover;
  z-index: -999;
}
.project:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #eee;
  opacity: 0.8;
  z-index: -888;
}
.project__title {
  padding: 20px 0;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.project__title .title__line {
  display: inline-block;
  width: 150px;
  border-top: 1px solid #ccc ;
}
.project__title .title__text {
  padding: 0 10px;
  vertical-align: -webkit-baseline-middle;
  font-size: 22px;
  color: #686868;
}
.project__container {
  flex: 1 1;
}
.project__block {
  padding: 0 5%;
  height: 100%;
}
.project__list {
