.work {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  background: url('../../../assets/img/bg/work.jpg') no-repeat;
  background-size: cover;
  z-index: -999;
}
.work:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #eee;
  opacity: 0.8;
  z-index: -99;
}
.work__title {
  padding: 20px 0;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.work__title .title__line {
  display: inline-block;
  width: 150px;
  border-top: 1px solid #ccc ;
}
.work__title .title__text {
  padding: 0 10px;
  vertical-align: -webkit-baseline-middle;
  font-size: 22px;
  color: #686868;
}
.work__plane{
  position: relative;
  left:-80px;
  height: 80px;
  width: 80px;
  animation: plane 15s ease-in-out infinite;
  animation-delay:2s;
}
@keyframes plane{
  0%{
    left: -180px;
  }
  100%{
    left: 100%;
  }
}
.work__plane img{
  height: 100%;
}
.work__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5% 15% 0; 
}
.work__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 250px;
  color: #555
}
.work__block .block__year {
  display: flex;
  flex-direction: column;
  height: 150px;
  width: 150px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 5px solid #555;
}
.work__block .block__year .year__text {
  font-size: 25px;
}
.work__block .block__bussiness {
  padding-top: 20px;
  font-size: 20px;
}
.work__block .block__job {
  padding-top: 20px;
  font-size: 18px;
  color: #EEB422;
}
.work__block .block__detail {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}
.work__block .block__detail .detail__text{
  line-height: 30px;
  font-size: 16px;
}
